import React, { Component } from 'react';
import { NavLink } from 'react-router-dom';
import Alert from 'react-bootstrap/Alert';
import Cookies from 'js-cookie';
import { Redirect } from "react-router-dom";

import {URL_API} from '../App';

class Login extends Component{
    constructor(props) {
        super(props);

        this.state = {
          alertShow: false, alertMessage : "", alertVariant:"",//Affichage, message et type de l'alerte
          email: "", password : "", //Input mail et password
          connected : false
        };
      }

      /**
       * Compare les identifiants avec la base de donne
       * Si les identifiants existent et sont bon, l'utilisateur est connect
       * @param {event} event Action du form par le bouton Submit
       */
      sendLogin(event) {
        event.preventDefault();
        const axios = require('axios').default;  //Requtes HTTP
        const sha256 = require('hash-anything').sha256; //Hash du mdp
        const url = URL_API+'getPrenom.php?mail='+this.state.email.toLowerCase()+'&password='+sha256(this.state.password);
        axios.get(url)
        .then(res => {
          if(res.data.id>0){
            //Se connecter
            Cookies.set("ID",res.data.id);
            Cookies.set("KEY",res.data.key);
            console.log("Vous tes connect, redirection vers la page principale !");
            this.setState({
              connected:true
            });
          }else{
            //Affichage en rouge du message de mdp incorrect
            console.log(res);
            this.setState({alertShow:true,alertMessage:"Adresse mail ou mot de passe incorrect.",alertVariant:"danger"});
          }
        })
        .catch(err => {
          console.log(err);
          //Affichage en jaune qu'il y a une erreur dans la requte
          this.setState({alertShow:true,alertMessage:"Une erreur s'est produite.",alertVariant:"warning"});
        });
      }
     
      /**
       * Met  jour la valeur du form dans lequel l'utilisateur crit
       * @param {event} event Appuie sur une touche
       */
      inputChange(event) {
        event.preventDefault();
        /* Mise  jour des valeurs des inputs */
        const { name, value } = event.target;
        this.setState({
          [name]: value
        })
      }

    render(){
        /* Utilisateur redirig si connect */
        if(this.state.connected){
          return(<Redirect to='/principale'/>); //Redirection vers la page principale
        }
      return(
        <div>
          {/* Alert affiche lorsque le couple (e-mail,mdp) n'est pas trouv dans la base de donne */}
          <Alert
          variant={this.state.alertVariant} 
          id="AlertIncorrect" 
          show={this.state.alertShow} 
          onClose={() => this.setState({alertShow:false})}
          dismissible>
            {this.state.alertMessage}
          </Alert>

          {/* Formulaire de connexion */}
          <div className="container-fluid">
            <form onSubmit={event => this.sendLogin(event)}>
              <div className="row">

                <div className="col-lg margetop5">
                  <input className="input"
                    name="email"
                    type="text"
                    placeholder="Ton adresse e-mail"
                    value={this.state.email}
                    onChange={event => this.inputChange(event)} 
                  />
                </div>


                <div className="col-lg margetop5">
                   <input className="input"
                      name="password"
                      type="password"
                      placeholder="Ton mot de passe"
                      value={this.state.password}
                      onChange={event => this.inputChange(event)} 
                    />
                </div>

              </div>
              <br/>
              <button className="btn-login" type="submit">Connexion</button>
            </form>
            <br/>
          </div>
          {/* Mot de passe oubli, redirection  dfinir, accueil pour l'instant */}
          <NavLink className="text-black" to="/" >Mot de passe oubli ?</NavLink>
        </div>
      );
    }
}

export default Login;